<template>
  <div style="position:fixed;margin:0px;height:100%;width:100%;    background-color: #c86dd7;
  background: -moz-linear-gradient(-45deg,  #3023ae 0%, #c86dd7 100%);
  background: -webkit-gradient(linear, left top, right bottom, color-stop(0%,#3023ae), color-stop(100%,#c86dd7));
  background: -webkit-linear-gradient(-45deg,  #3023ae 0%,#c86dd7 100%) ;
  background: -o-linear-gradient(-45deg,  #3023ae 0%,#c86dd7 100%);
  background: -ms-linear-gradient(-45deg,  #3023ae 0%,#c86dd7 100%);
  background: linear-gradient(135deg,  #3023ae 0%,#c86dd7 100%);
  background-attachment: fixed;
  ">
  <mu-appbar  title="我的资料">
   <mu-icon-button icon="arrow_back" slot="left" @click="()=>this.$router.push({path:'/index'})"/>
 </mu-appbar>
 

 <mu-list >

    <mu-list-item :title="'用户名：'+username" >
     
    </mu-list-item>
    <mu-divider />
    <mu-list-item :title="'到期时间：'+endtime">
     
    </mu-list-item>
    <mu-divider />
    <mu-list-item :title="'会员等级：'+userlevel">
     
    </mu-list-item>
    
     
  </mu-list>
    <div class="center" style="color:#fff;">
     <h2>只要二包烟，整月快乐似神仙</h2>
     <h2>一顿小烧烤，半年都很屌！</h2>
    <mu-row>
     <mu-col width="25">
       5元／1天
     </mu-col>
     <mu-col width="25">
       20元／10天
     </mu-col>
     <mu-col width="25">
       50元／1月
     </mu-col>
     <mu-col width="25">
       168元／半年
     </mu-col>
  </mu-row>

    <br>
      <h3>如您遇到支付问题请联系客服</h3>
      <h3>支付成功后，如果VIP会员资格没有生效，请退出重新登录即可</h3>
      <mu-raised-button v-if="!vip" label="充值VIP" @click="showDialog(1)"  secondary/>
      <mu-raised-button v-if="vip" label="充值" @click="showDialog(1)" secondary/>
      <mu-raised-button label="联系客服" secondary @click="showDialog(1)"/>
    </div>
  
   <mu-dialog :open="dialog" :title="title"  @close="hideDialog">
     
      <h6 style="color:#FF0000;margin:0px;">{{info}}</h6> 
      <div v-if="type==0">
      <mu-raised-button label="查看如何留言"  v-if="!showtipimage" secondary @click="toggle"/>
      <mu-raised-button label="返回充值二维码"  v-if="showtipimage" secondary @click="toggle"/>
      <img src="https://i.loli.net/2017/09/13/59b906d0708de.png"  v-if="showtipimage" height="auto">
      <img :src="codeurl" v-if="!showtipimage"  height="auto">
      </div>
      <div v-if="type==1">
     
      <a href="mqqwpa://im/chat?chat_type=wpa&uin=3026824138&version=1&src_type=web&web_src=kjpav.com">在线客服:qq3026824138</a>
      </div>
     
    <mu-flat-button  label="關閉" slot="actions" primary @click="hideDialog"/>
 </mu-dialog>
</div>
</template>
<script >
  export default {
    name:'login',
    data(){
      return {
        dialog:false,
        codeurl:"",
        type:1,
        title:"",
        info:"",
        showtipimage:false,
     }
   },
   computed:{
    endtime(){
      var date= new Date(this.$store.state.loginedUser['endtime']*1000)
      return date.getFullYear()+"-"+(date.getMonth()+1)+"-"+date.getDate()+"  "+date.getHours()+"时:"+date.getMinutes()+"分:"+date.getSeconds()+"秒"
    },
    vip(){
      return this.$store.state.loginedUser['endtime']*1000>new Date().getTime()
    },
    username(){
      return this.$store.state.loginedUser['username']
    },
    userlevel(){
        return this.vip?"VIP":"普通会员"
    }, 
  },
  methods:{
    showDialog(t){
       this.dialog=true
       this.type=t;

       if(t){
          this.title="添加客服"
          this.info="联系客服请注明夜来香，否则客服不会验证通过!"
          this.codeurl="https://i.loli.net/2017/09/12/59b7bfc8c1f2a.png"
         
       }
       else {
        this.showtipimage=false
        this.title="充值VIP"
        this.info="注意！付款时请务必在留言处填写您在本网站用户名！否则无法准确为您充值！"
         this.codeurl="https://i.loli.net/2017/09/12/59b7bffc265d6.png"
       }
    },
    hideDialog(){
      this.dialog=false
    },
    toggle(){
       this.showtipimage=!this.showtipimage
    }
  }
 }
  

    
  </script>
  <style scoped>
    
   .flex{
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: flex-start;
  }
  .center{
   margin: 30px;
   margin-top: 30px;
   text-align: center;
   
 }
 .desc-container{
  height:100%;
  overflow-y: scroll;
}
img {
  width: 100%;
}
.fixed {
  position:fixed;
  top: 0;

}

</style>


